<template>
	<div class="page2_1">
		<LeftNav/>
		<div class="nav">
			<ul class="fl">
				<li v-for="item in list">
					<router-link active-class="active" class="item" :to="{path:item.page}">
					{{item.name}}
					</router-link>
				</li>
			</ul>
			<!-- <div class="fr inter">
				<interBtn title="合规审计" id="more1"/>
			</div> -->
		</div>
		<div class="content_page">
			<router-view/>
		</div>
	</div>
</template>
<script type="text/javascript">
	// import interBtn from './interactions'
	import LeftNav from './zhibiao'
	export default{
		name:'page2_1',
		components:{
			// interBtn,
			LeftNav
		},
		data(){
			return{
				list:[
				{name:'与政策法规的比较',page:'/inside/page2_1/page2_1_1'},
				{name:'与行业规范的比较',page:'/inside/page2_1/page2_1_2'},
				{name:'与业务逻辑的比较',page:'/inside/page2_1/page2_1_3'},
				{name:'与勾稽关系的比较',page:'/inside/page2_1/page2_1_4'},
				]
			}
		},
		computed:{
			company(){
				return JSON.parse(this.$store.getters.getCompany);
			},
			userName(){
				return sessionStorage.getItem('username');
			},
		}
	}
</script>
<style type="text/css" scoped="">

</style>


